import React from 'react'
import Nav from '../Nav/Nav'

function Tarif() {
  return (
    <main className="main">
      <div className="container">
        <Nav />
        <section className="main__profile profile tarif">
          <div className="profile__body">
            <h1 className="profile__title title"></h1>
            <p className="text text_size_normal tarif__subtitle">    Infinity Post, Infinity
              Browser, infinity incognito ( ),        .    990    
               INFY.</p>
            <section className="banner__buttons wallet__buttons">
              <div className="tarif__card">
                <p className="text text_size_normal tarif__text tarif__text_type_email">    </p> <a
                  className="wallet__button link link_active" href="/tarif"></a>
              </div>
              <div className="tarif__card">
                <p className="text text_size_normal tarif__text tarif__text_type_browser">   </p> <a
                  className="wallet__button link link_active" href="/tarif"></a>
              </div>
              <div className="tarif__card">
                <p className="text text_size_normal tarif__text tarif__text_type_messager">   </p> <a
                  className="wallet__button link link_active" href="/tarif"></a>
              </div>
            </section>
          </div>
        </section>
      </div>
    </main>
  )
}

export default Tarif
